//nextui
import { Button } from "@nextui-org/react";

export default function AboutUs(){
  const data = {
    title : "  ",
    subtitle : "                                                                                          ",
    image : "assets/images/video.png"
  }
  return(
    <section className="BaseContainer grid lg:grid-cols-2 gap-5">
      <div className="flex flex-col items-start gap-5">
        <span data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" className="font-MorabbaSemiBold text-3xl text-MainColor-50">{data.title}</span>
        <p data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="1000" className="font-PinarMedium text-justify text-lg">{data.subtitle}</p>
        <Button data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="2000" color="primary" className="text-black font-PinarSemiBold self-end"> </Button>
      </div>
      <div>
        <img data-aos-once="true" data-aos="flip-left" data-aos-duration="500" data-aos-delay="3000" className="mr-auto w-full lg:w-auto lg:h-[300px]" src={data.image} alt="" />
      </div>
    </section>
  );
}